.MenuBtn{
	display: none;
	height: 35px;
	width: 35px;
  	-webkit-animation-name: Menub;
  	        animation-name: Menub;
	-webkit-animation-duration: 3s;
	        animation-duration: 3s;
	-webkit-animation-iteration-count:1;
	        animation-iteration-count:1;
	position: fixed;
	right: 0;
	margin-right: 10px;
}

@-webkit-keyframes Menub {
  from {
	-webkit-transform:  scale(0,0);
	        transform:  scale(0,0);
    opacity: 0;
  }
  to {
	  
	-webkit-transform:  scale(1,1);
	  
	        transform:  scale(1,1);
    opacity: 1;
}}

@keyframes Menub {
  from {
	-webkit-transform:  scale(0,0);
	        transform:  scale(0,0);
    opacity: 0;
  }
  to {
	  
	-webkit-transform:  scale(1,1);
	  
	        transform:  scale(1,1);
    opacity: 1;
}}
.MenuBtn:hover{
	background-color: orange;
	-webkit-transform:  scale(1.05,1);
	        transform:  scale(1.05,1);
}



.NavList{
