}



.DockedMenu{
    display: none;
    width: 60%;
	float: right;
	right: 0;
    top: 0;
    bottom: 0;
	position: fixed;
	background-color: white;
	z-index: 6;
	font-weight: bolder;
	opacity: .9;
	background: #9bc3d1;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to left, #0f0e17, #9bc3d1); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

	border-left: 1px #f5f55f solid;
	-webkit-animation: DMenub 1s 1;
	        animation: DMenub 1s 1;

}

@-webkit-keyframes DMenub {
  from {
	-webkit-transform: translate(500px,0);
	        transform: translate(500px,0);
    opacity: 0;
  }
  to {
	-webkit-transform: translate(0px,0);
	        transform: translate(0px,0);
    opacity: 1;
}}

@keyframes DMenub {
  from {
	-webkit-transform: translate(500px,0);
	        transform: translate(500px,0);
    opacity: 0;
  }
  to {
	-webkit-transform: translate(0px,0);
	        transform: translate(0px,0);
    opacity: 1;
}}

@-webkit-keyframes DMenubAfter {
  from {
	-webkit-transform: translate(0,0);
	        transform: translate(0,0);
    opacity: 1;
  }
  to {
	-webkit-transform: translate(500px,0);
	        transform: translate(500px,0);
    opacity: 0;
}}

@keyframes DMenubAfter {
  from {
	-webkit-transform: translate(0,0);
	        transform: translate(0,0);
    opacity: 1;
  }
  to {
	-webkit-transform: translate(500px,0);
	        transform: translate(500px,0);
    opacity: 0;
}}

