    width: 100%;
    padding-bottom: 0;
  }
  .home-msg{
    line-height: normal;
    width: 50%;
	  font-size: 14px;
  }
  .Logo{
    
	margin-right: 0;
  }
  .navBarInside > h3{
    margin-bottom: 0;
  }
  .FooterAddressBoxMap {
